.button {
  width: 100%;
  max-width: 175px;
  padding: 0.5rem;
  font: inherit;
  background-color: #f8f9fa;
  border: none;
  border-radius: 5px;
  -webkit-transition: all 0.25s;
  transition: all 0.25s;
  cursor: pointer;
}

.button:hover {
  background-color: rgba(248, 249, 250, 0.8);
}
